import React from "react";
import { Link } from "react-router-dom";

const ManageContent = () => {
  return (
    <div className="container mt-5">
      <h1>إدارة المحتوى</h1>
      <div className="row mt-4">
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">المدونات</h5>
              <p className="card-text">إضافة أو تعديل أو حذف المدونات.</p>
              <Link to="/admin/manage-blogs" className="btn btn-primary">
                إدارة المدونات
              </Link>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">المشاريع</h5>
              <p className="card-text">إضافة أو تعديل أو حذف المشاريع.</p>
              <Link to="/admin/manage-projects" className="btn btn-primary">
                إدارة المشاريع
              </Link>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title">الأخبار</h5>
              <p className="card-text">إضافة أو تعديل أو حذف الأخبار.</p>
              <Link to="/admin/manage-news" className="btn btn-primary">
                إدارة الأخبار
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ManageContent;
